export class MapirComponent implements AfterViewInit /*, OnChanges, OnInit*/ {
  @Input() polygonInfo: { polygon: LatLngBoundsExpression; fitBound: boolean };
  @Input() layerOptions: WMSOptions;
  @Input() mapPosition: LatLng;
  @Input() markerPosition: LatLng = new LatLng(34, 51);
  @Input()
  markerIconUrl: String =
    "data:image/svg+xml;base64,";
  @Input("zoom") mapZoom: Number = 10;
  @Output() viewChange = new EventEmitter<boolean>();
  @Output() markerPositionChange = new EventEmitter<LatLng>();

  private map: Map;
  private currentLayer: TileLayer.WMS;
  private readonly layerBaseUrl = "https://map.ir/shiveh";
  private markerIcon: Icon;
  private currentPolygon: Polygon;
  private currentMarker: Marker;
  private isRoadView = false;

  ngAfterViewInit(): void {
    this.initMap();
  }

  getViewPortCenter(): LatLng {
    return this.map.getBounds().getCenter();
  }

  private initMap() {
    this.map = L.map("mapp", {
      center: this.mapPosition,
      zoom: this.mapZoom,
      layers: [
        L.tileLayer.wms(this.layerBaseUrl, {
          layers: "Shiveh:ShivehGSLD256",
          format: "image/png"
        })
      ],
      attributionControl: false,
      zoomControl: false
    });

    this.markerIcon = L.icon({
      iconUrl: this.markerIconUrl,
      iconSize: [48, 48],
      iconAnchor: [24, 48]
    });

    this.currentMarker = L.marker(this.markerPosition, {
      icon: this.markerIcon,
    }).addTo(this.map);
  }
}
